@use 'base/variables' as v;

.citas {
    list-style: none;
    padding: 0;

    li{
        padding: 1rem;
        border-top: 1px solid black;

        p{
            font-weight: v.$bold;
            color: blue;
        }

        span,
        .servicio{
            font-weight: v.$regular;
            color: black;
        }

        &:hover{
            background-color: lightblue;
        }
    }
}
.servicios {
    @extend .citas;

    .acciones {
        margin: 0;
        }
    .boton,
    .boton-eliminar {
        margin: 0;
    }
    }
